const Base = require('../base.js');
//修改文章
module.exports = class extends Base {
    async indexAction() {
        const model = this.model('article');
        var sd_ddf = this.ctx.post(),
            s_xig_s = {}
        s_xig_s.title = sd_ddf.biaoti
        s_xig_s.text = sd_ddf.sdf_drt
        let affectedRows = await model.where({
            id: sd_ddf.id
        }).update(s_xig_s);

        var jhde = {}
        if (affectedRows == 1) {
            jhde.code = 0
            jhde.msg = "修改文章成功"


        } else {
            jhde.code = -1
            jhde.msg = "修改文章失败"
        }
        return this.fail(jhde); //2输出json 推荐

    }

};
